<template>
  <div>
    <crumbs menu='系统管理' subMenu='用户管理' /> 
    
    <div class="container">
      <div class="handle-box">
        <!-- <el-button
          type="primary"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delAllSelection"
          >批量删除</el-button
        > -->
        <el-select
          v-model="query.role"
          placeholder="用户角色"
          class="handle-select mr10"
          clearable
        >
          <el-option key="1" label="商场用户" value="商场用户"></el-option>
          <el-option key="2" label="集团用户" value="集团用户"></el-option>
          <el-option key="3" label="店铺用户" value="店铺用户"></el-option>
          <el-option key="4" label="普通用户" value="普通用户"></el-option>
        </el-select>
        <el-select
          v-model="query.state"
          placeholder="状态"
          class="handle-select mr10"
          clearable
        >
          <el-option key="1" label="正常" value="正常"></el-option>
          <el-option key="2" label="锁定" value="锁定"></el-option>
        </el-select>
        <el-input
          v-model="query.name"
          placeholder="用户名"
          class="handle-input mr10"
        ></el-input>
        <el-button type="primary" icon="el-icon-search" @click="handleSearch"
          >搜索</el-button>
        <el-button type="primary" icon="el-icon-search" @click="handleAdd"
          >添加用户</el-button>
           <export-excel :file-name="_fileName" :header="['ID','用户名','角色类型','真实姓名','注册时间','所属商场','状态']"
              :filter-filed="['id','name','role','trueName','date','shop','state']" :data="showTableData">导出</export-excel>
      </div>
      <el-table
        :data="
          showTableData.slice(
            (currentPage - 1) * pagesize,
            currentPage * pagesize
          )
        "
        border
        class="table"
        ref="multipleTable"
        header-cell-class-name="table-header"
        @selection-change="handleSelectionChange"
      >
        <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
        <el-table-column
          prop="id"
          label="ID"
          width="55"
          align="center"
        ></el-table-column>
        <el-table-column prop="name" label="用户名"></el-table-column>
        <el-table-column label="角色类型">
          <template #default="scope">{{ scope.row.role }}</template>
        </el-table-column>
        <el-table-column prop="trueName" label="真实姓名"></el-table-column>
        <el-table-column prop="date" label="注册时间"></el-table-column>
        <el-table-column label="所属商场" align="center">
          <template #default="scope">
            {{ scope.row.shop }}
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center">
          <template #default="scope">
            <el-tag
              :type="
                scope.row.state === '正常'
                  ? 'success'
                  : scope.row.state === '锁定'
                  ? 'danger'
                  : ''
              "
              >{{ scope.row.state }}</el-tag
            >
          </template>
        </el-table-column>

        <el-table-column label="操作" width="180" align="center">
          <template #default="scope">
            <el-button
              type="text"
              icon="el-icon-edit"
              @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              type="text"
              icon="el-icon-delete"
              class="red"
              @click="handleDelete(scope.$index, scope.row)"
              >{{
                scope.row.state === "锁定" ? "解锁用户" : "锁定用户"
              }}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination ">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[2, 5, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>

    <!-- 编辑弹出框 -->
    <el-dialog title="编辑" v-model="editVisible" width="30%" :show-close='false' :close-on-click-modal='false'>
      <el-form ref="form" :model="form" label-width="130px">
        <el-form-item label="用户名">
          <el-input v-model="form.name" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="真实姓名">
          <el-input v-model="form.trueName" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址">
          <el-input v-model="form.emall" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="form.phone" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="账号有效期">
          <el-date-picker
            style='width:53%;'
              v-model="form.time"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="所属商场">
          <el-select
            v-model="shopSelect"
            filterable
            :placeholder="suoshushangchang"
          >
            <el-option
              v-for="item in shopList"
              :key="item.value"
              :label="item.label"
              :value="item.label"
              :disabled="item.disabled"
            >
            </el-option>
          </el-select>

          <!-- <el-input v-model="form.shop"></el-input> -->
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleCancel" >取 消</el-button>
          <el-button type="primary" @click="saveEdit">确 定</el-button>
        </span>
      </template>
    </el-dialog>


    <!-- 添加弹出框 -->
    <el-dialog title="添加用户" v-model="addVisible" width="30%">
      <el-form ref="addForm" :model="addForm" label-width="130px">
        <el-form-item label="用户名" >
          <el-input v-model="addForm.name" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="真实姓名">
          <el-input v-model="addForm.trueName" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="邮箱地址">
          <el-input v-model="addForm.emall" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="addForm.phone" style='width:53%;'></el-input>
        </el-form-item>
        <el-form-item label="账号有效期">
            <el-date-picker
            style='width:53%;'
              v-model="addForm.time"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="所属商场">
          <el-select
            v-model="addForm.shopSelect"
            filterable
            :placeholder="请输入"
          >
            <el-option
              v-for="item in shopList"
              :key="item.value"
              :label="item.label"
              :value="item.label"
              :disabled="item.disabled"
            >
            </el-option>
          </el-select>

          <!-- <el-input v-model="form.shop"></el-input> -->
        </el-form-item>
        <el-form-item label="角色类型">
          <el-select
            v-model="addForm.roleSelect"
            filterable
          >
            <el-option
              v-for="item in roleList"
              :key="item.value"
              :label="item.label"
              :value="item.label"
              :disabled="item.disabled"
            >
            </el-option>
          </el-select>
          <!-- <el-input v-model="form.shop"></el-input> -->
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="saveAdd">确 定</el-button>
          <el-button @click="addVisible = false">取 消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
