          shop: "中百仓储",
        },
      ],
      searchTableData:[],
      shopSelect: "",
      roleSelect:'',
      multipleSelection: [],
      delList: [],
      editVisible: false,
      addVisible:false,
      pageTotal: 8,
      form: {},
      addForm:{
          name:'',
          trueName:'',
          shopSelect:'',
          roleSelect:'',
          phone:'',
          emall:'',
          time:''

      },
      dataCache:'',
      idx: -1,
      id: -1,
      suoshushangchang: "",
      currentPage: 1, //当前页
      pagesize: 5, //每页数量
      total: 0, //
