  // },
    mounted() {
    this.total = this.showTableData.length;
  },
  methods: {
    // 获取 easy-mock 的模拟数据
    // getData() {
    //     fetchData(this.query).then(res => {
    //         console.log(res);
    //         this.tableData = res.list;
    //         this.pageTotal = res.pageTotal || 50;
    //     });
    // },
    // 触发搜索按钮
    handleSizeChange(val) {
      this.pagesize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleCancel(){
      this.editVisible = false;
      this.tableData[this.idx] =JSON.parse(this.dataCache) ;
      this.showTableData[this.idx] =JSON.parse(this.dataCache) ;
      console.log(this.tableData[this.idx])
      this.dataCache ='';
    },
    handleSearch() {
        // 进行搜索
        var _this = this;
        this.tableData.forEach(function(items){
                console.log(1,items.role,2,_this.query.role);
                if(items.role == _this.query.role || _this.query.role == "" ){
                  if (items.name == _this.query.name || _this.query.name == "") {
                    if(items.state==_this.query.state||_this.query.state==''){
                      _this.searchTableData.push(items)
                    }
                      
                  }
                }
            });
          this.total=this.searchTableData.length
            if (_this.searchTableData.length != 0 ) {
                _this.showTableData = _this.searchTableData;
                _this.searchTableData = [];
            } else {
                _this.$message.error(`没有查询到记录`);
            }
        
    //   this.$set(this.query, "pageIndex", 1);
    //   this.getData();
    },
    handleAdd(){
        this.addVisible = true;
    },
    saveAdd(){
        // 添加用户
         var aData = new Date();
    // console.log(aData) //Wed Aug 21 2019 10:00:58 GMT+0800 (中国标准时间)
        var dataValue = aData.getFullYear() + "-" + (aData.getMonth() + 1) + "-" + aData.getDate();
        let timeRange=this.$moment(this.addForm.time[0]).format('YYYY-MM-DD')+'~'+this.$moment(this.addForm.time[1]).format('YYYY-MM-DD')
        console.log(this.addForm.name,dataValue, this.roleSelect, this.addForm.shopSelect);
        var newTableData = this.showTableData
        newTableData.unshift({
            id: 11,
            name: this.addForm.name,
            role: this.addForm.roleSelect,
            trueName: this.addForm.trueName,
            state: "正常",
            date: dataValue,
            shop: this.addForm.shopSelect,
            time:timeRange,
            emall:this.addForm.emall,
            phone:this.addForm.phone
        })
        this.showTableData = newTableData;
        this.TableData = newTableData;
        this.searchTableData = newTableData;

         this.total =  this.total + 1
        // console.log(this.showTableData)
        this.addVisible = false
        this.$message.success(`成功添加一个用户`);
        this.marketDataCache ='';
        
    },
    // 删除操作
    handleDelete(index) {
      var idx = index + this.pagesize * (this.currentPage -1)
      // 二次确认删除
      this.$confirm("确定要锁定用户吗？", "提示", {
        type: "warning",
      }).then(() => {
          if (this.tableData[idx].state == "正常") {
            this.$message.success("锁定成功");
            this.tableData[idx].state = "锁定";
            this.showTableData[idx].state = "锁定";
            this.searchTableData[idx].state = "锁定";


          } else {
            this.$message.success("解锁成功");
            this.tableData[idx].state = "正常";
            this.showTableData[idx].state = "正常";
            this.searchTableData[idx].state = "正常";

          }
        })
        .catch(() => {});
    },
    // 多选操作
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delAllSelection() {
      const length = this.multipleSelection.length;
      let str = "";
      this.delList = this.delList.concat(this.multipleSelection);
      for (let i = 0; i < length; i++) {
        str += this.multipleSelection[i].name + " ";
      }
      this.$message.error(`删除了${str}`);
      this.multipleSelection = [];
    },
    // 编辑操作
    handleEdit(index, row) {
      this.dataCache = JSON.stringify(row) 
      this.suoshushangchang = row.shop;
    //   console.log(row.shop);
      this.idx = index + this.pagesize * (this.currentPage -1 );
      console.log(111,this.pagesize*(this.currentPage -1 ));
      this.form = row;
      try {
        if(row){
        this.form.time=[this.$moment(row.time.split('~')[0]).format('YYYY-MM-DD'),this.$moment(row.time.split('~')[1]).format('YYYY-MM-DD')]
        console.log(row.time)
      }
      } catch (error) {
        console.log(error)
      }
      
      
      this.editVisible = true;
    },
    // 保存编辑
    saveEdit() {
      this.editVisible = false;
      this.$message.success(`修改第 ${this.idx + 1} 行成功`);
      // this.form.time=
      // this.$set(this.tableData, this.idx, this.form);
      this.tableData[this.idx]=this.form
      this.showTableData[this.idx] = this.form;

      console.log(this.tableData[this.idx].shop, this.shopSelect);
      this.tableData[this.idx].shop = this.shopSelect;
      this.showTableData[this.idx].shop = this.shopSelect;
      // this.searchTableData[this.idx].shop = this.shopSelect;


    },
    // 分页导航
